import React, { useEffect } from "react";
import "./Page1.scss";
import * as ScrollMagic from "scrollmagic"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBars, faArrowCircleDown } from "@fortawesome/fontawesome-free-solid";
import $ from "jquery"
const Page1 = () => {

   useEffect(() => {
    $(".hamburger").click(function(){
        $(".banner").toggleClass("banner2")
    })
   })

   useEffect(() =>{
    const controller = new ScrollMagic.Controller();

    var scene = new ScrollMagic.Scene({
        triggerElement: ".banner",
        triggerHook: 0.8,
        duration: "110%",
    })
    .setClassToggle(".banner", "visible")
 
    .addTo(controller);

    var scene1 = new ScrollMagic.Scene({
        triggerElement: ".hi",
        triggerHook: 0.8,
        duration: "110%",
    })
    .setClassToggle(".hi", "visible")
 
    .addTo(controller);
   })

   
   
       
  
    
    return (
        <section>
            <div>
                <div className="animation-area">
                    <ul className="box-area">
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                    </ul>
                </div>
                <div className="Page1">
                    <nav>
                    <a id="hamburger" ><FontAwesomeIcon icon={faBars} className="hamburger"  size="5x" /></a>
                        <ul className="banner" id="banner">
                            <a id="animateHome"  href="#main1"><li>Home</li></a>
                            <a id="animateAbout"  href="#about"><li>Projects</li></a>
                           <a id="animateContact"  href="#contact"><li>Contact</li></a>
                        </ul>
                    </nav>
                    <div className="main1" id="main1">
                        <p className="hi">Hello, I'm Matt, <br /> I'm a <span>front-end developer</span>.</p>
                    </div>

                    <div className="arrowan">
                    <FontAwesomeIcon icon={faArrowCircleDown} className="arrow"  size="2x" />
                    <FontAwesomeIcon icon={faArrowCircleDown} className="arrow"  size="2x" />
                    <FontAwesomeIcon icon={faArrowCircleDown} className="arrow"  size="2x" />
                    </div>

                </div>
            </div>
        </section>
        )
    }


export default Page1;